<template>
    <div calss="login">
        <div v-if="!loginOK">
        帳號:<input v-model="username"/>
        密碼:<input type="password" v-model="password"/>
        <button @click="login">登入</button>
        </div>
        <user v-if="loginOK" />
        <button @click="logout" v-if="loginOK">logout</button>
    </div>
</template>
<script>
import axios from 'axios'
import user from './user'
export default{
    name:'login',
    components:{
        user
    },
    data(){
        return{
            username:'',
            password:'',
            loginOK:false,
            respdata:[]
        }
    },
    methods:{
        login(){
            axios
            .post("http://127.0.0.1:8081/epay/login",
            this.$qs.stringify({
                user:this.username,
                pwd:this.password
            })
            )
            .then(response=>{
                this.respdata=response.data
                console.log(this.respdata)
                if(this.respdata=="無此帳號"||this.respdata=="密碼錯誤"){
                    alert("帳號密碼錯誤")
                }else{
                    this.$router.push({name:"user",params:{name:this.username,sessionid:this.respdata}})
                    this.loginOK=true
                }    
                })
            .catch(error=>error)
        },
        logout(){
                this.username=''
                this.password=''
                this.loginOK=false
                this.$router.push("/")
            }
    }
    
}
</script>