<template>
  <div class="FROM" style="width:300px;height:600px;border-style:double;margin:0px auto;"><h2>用戶登錄表</h2>
    <span v-if="reqMers==null">{{quireMers()}}</span>
    <div style="text-align:right;margin:0px auto;padding:5px;" v-for="(lab,index) in labels" :key='lab.name'>
      <div v-if="index==2">
        <label>{{lab.name}}:</label>
        <select v-model="lab.value">
          <option v-for="product in products" :key="product.name" :value='product.type'>
            {{product.name}}
          </option>
        </select>
      </div>
      <div v-else-if="index==6||index==7">
        <label>{{lab.name}}:</label>
        <input type="password" v-model="lab.value"/>
      </div>
      <div v-else>
        <label>{{lab.name}}:</label>
        <input v-model="lab.value"/>
      </div>
    </div>
    <div style="text-align:right;">
    <div style="padding:25px;">
      <button @click="updata">添加登錄用戶</button>
    </div>
    <div style="padding:5px;">
      刪除用戶:<select v-model="delid">
        <option v-for="n in reqMers" :key="n" >
          {{n}}
        </option>
      </select>
      <button @click="deleteData">delete</button>
    </div>
    <div style="padding:5px;">
      查詢用戶:<select v-model="quireid">
        <option v-for="n in reqMers" :key="n" >
          {{n}}
        </option>
      </select>
      <button @click="selectMerinfo">select</button>
    </div>
    </div>
    <span v-if="merInfo!=null">
      <show v-bind:data="merInfo"/>
    </span>
  </div>
</template>
<script>
import axios from 'axios'
import show from "./show";
export default {
  name: "FROM",
  components:{
    show
  },
  data(){
    return{
      delid:'',
      quireid:'',
      products:[{name:'企業',type:0},{name:'個人',type:1},{name:'代理商',type:2}],
      labels:[
        {name:'商戶號',value:null},
        {name:'網址地址',value:null},
        {name:'商戶類型',value:null},
        {name:'電子信箱',value:null},
        {name:'手機',value:null},
        {name:'登錄名',value:null},
        {name:'登錄密碼',value:null},
        {name:'支付密碼',value:null}
      ],
      reqMers:null,
      merInfo:null
    }
  },
  methods:{
    updata(){
      axios
      .post("http://127.0.0.1:8081/epay/data",
      this.$qs.stringify({
            taccountid: this.labels[0].value,
            merurl:this.labels[1].value,
            mertype:this.labels[2].value,
            email:this.labels[3].value,
            phone:this.labels[4].value,
            id:this.labels[5].value,
            pwd:this.labels[6].value,
            paypwd:this.labels[7].value
      }))
      .then(response=>{
        alert("添加完成!")
        this.quireMers()
        console.log(response.data)
      })
      for(var i=0;i<this.labels.length;i++){
        this.labels[i].value=null;
      }
    },
    deleteData(){
      axios
      .post("http://127.0.0.1:8081/epay/delete",
      this.$qs.stringify({
            taccountid: this.delid,
      }))
      .then(response=>{
        alert("刪除完成!")
        this.quireMers()
        console.log(response.data)
      })
    },
    selectMerinfo(){
      axios
      .post("http://127.0.0.1:8081/epay/quireMerinfo",
      this.$qs.stringify({
            taccountid: this.quireid,
      }))
      .then(response=>{
        this.merInfo=response.data
        console.log(this.merInfo)
      })
    },
    quireMers(){
      axios
      .post("http://127.0.0.1:8081/epay/quire")
      .then(response=>{
        this.reqMers=response.data
        console.log(this.reqMers)
      })
    }
  }
};
</script>